/* Button Styles */
.theme-toggle-button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 24px;
    color: #333; /* Customize the color */
    padding: 8px;
    margin: 0;
    outline: none; /* Remove focus outline */
    transition: color 0.2s ease-in-out; /* Add smooth color transition */
  }
  
  .theme-toggle-button:hover {
    
    color: var(--p30-percent);
    transform: scale(1.2);
  }


  label {
    width:60px;
    height:30px;
    position: relative;
    display: block;
    background: #ebebeb;
    border-radius: 200px;
    box-shadow: inset 0px 5px 15px rgba(0,0,0,0.4), inset 0px -5px 15px rgba(255,255,255,0.4);
    cursor: pointer;
  }
  label:after {
    content: "";
    width:30px;
    height: 30px;
    position: absolute;
    top:0px;
    left:0px;
    background: linear-gradient(180deg,#ffcc89,#d8860b);
    border-radius: 50px;
    box-shadow: 0px 5px 10px rgba(0,0,0,0.2);
  }
  input {
    width: 0;
    height: 0;
    visibility: hidden;
  }
  input:checked + label {
    background: #242424;
  }
  input:checked + label:after {
    left:60px;
    transform: translateX(-100%);
    background: linear-gradient(180deg,#777,#3a3a3a);
  }
  label, label:after {
    transition: 0.3s
  }

  label:active:after{ 
    width: 60px; 
  }

  input:checked + label + .background {
    background:#242424;
  }

  label svg {
    position: absolute;
    width: 60px;
    top:0px;
    z-index: 100;
  }
  label svg.sun {
    left:-15px;
    top: 2px;
    fill:#fff;
    transition: 0.3s;
  }
  label svg.moon {
    left:15px;
    top: 2px;
    fill:#7e7e7e;
    transition: 0.3s;
  }
  input:checked + label svg.sun {
    fill:#7e7e7e;
  }
  input:checked + label svg.moon {
    fill:#fff;
  }